import Image from "next/image";
import { ArrowUpRight } from "lucide-react";

const Blog = () => {
  return (
    <div className="bg-black fomt-header ml-[82px] text-white p-10">
      <h1 className="text-4xl w-[290px] h-[99px] text-[66px] leading-[99px] font-bold mb-6">
        Our blog
      </h1>

      {/* 1st Row */}

      <div className="flex flex-col mt-8 md:flex-row items-center space-y-6 md:space-y-0 md:space-x-10">
        {/* Left: Image */}
        <div className="rounded-3xl overflow-hidden">
          <Image
            src="/feature-3.png" // Replace with actual image path
            alt="Adventure Woman"
            width={500}
            height={300}
            className="object-cover rounded-3xl"
          />
        </div>

        {/* Right: Text Section */}
        <div className="max-w-md">
          <h2 className="text-[36px] font-header leading-[40px] w-[287px] h-[80px] font-extrabold">
            Adventure woman
          </h2>
          <div className="w-[434px] h-[6px] font-header rounded-full bg-[#FF5C00] my-2"></div>
          <p className="text-[22px] inline-block font-header leading-[33px] font-medium w-[310px] h-[99px]">
            In row boat taking photo on smartphone of beautiful fjord lake for
            social media. {/* Floating Arrow Button */}
          </p>

          {/* Floating Arrow Button */}
          <button className=" ml-6 inline-block mt-2 size-[95px]  bg-[#FF5C00]  rounded-full shadow-lg hover:bg-orange-600 transition">
            <ArrowUpRight
              size={24}
              className="text-white size-[50px] mx-auto"
            />
          </button>
        </div>
      </div>

      {/* 2nd Row */}

      <div className="flex flex-col mt-16 md:flex-row  items-center space-y-6 md:space-y-0 md:space-x-10">
        {/* Right: Text Section */}
        <div className="max-w-md">
          <h2 className="text-[36px] font-header leading-[40px] w-[287px] h-[40px] font-extrabold">
            5 Lakes
          </h2>
          <div className="w-[434px] h-[6px] font-header rounded-full bg-[#FF5C00] my-2"></div>
          <p className="text-[22px] inline-block font-header leading-[33px] font-medium w-[318px] h-[165px]">
            Located in Cumbria, United Kingdom, Lakes is home to an impressive
            selection of attractions and experiences, making it well worth a
            visit. {/* Floating Arrow Button */}
          </p>

          {/* Floating Arrow Button */}
          <button className=" ml-6 inline-block mt-2 size-[95px]  bg-[#FF5C00]  rounded-full shadow-lg hover:bg-orange-600 transition">
            <ArrowUpRight
              size={24}
              className="text-white size-[50px] mx-auto"
            />
          </button>
        </div>
        {/* Left: Image */}
        <div className="rounded-3xl overflow-hidden">
          <Image
            src="/feature-1.png" // Replace with actual image path
            alt="Lake"
            width={500}
            height={300}
            className="object-cover rounded-3xl"
          />
        </div>
      </div>
    </div>
  );
};

export default Blog;
